import React from 'react';
import { navigate } from 'gatsby';

import BlogPreviewGrid from '../../components/BlogPreviewGrid';
import Container from '../../components/Container';
import Hero from '../../components/Hero';
import Layout from '../../components/Layout/Layout';
import ThemeLink from '../../components/ThemeLink';

import { generateMockBlogData } from '../../helpers/mock';
import * as styles from './index.module.css';

const BlogPage = (props) => {
  const blogData = generateMockBlogData(3);

  return (
    <Layout disablePaddingBottom>
      <div className={styles.root}>
        <Hero
          maxWidth={'400px'}
          image={'/blogCover.png'}
          title={`Title`}
          ctaLink={'Call To Action'}
          ctaTo={'/blog/sample'}
          header={'tbd'}
        />

        <div className={styles.navContainer}>
          <ThemeLink
            onClick={() => navigate('/blog/one')}
            to={'/blog/one'}
          >
            Project One
          </ThemeLink>
          <ThemeLink
            onClick={() => navigate('/blog/two')}
            to={'/blog/two'}
          >
            Project Two
          </ThemeLink>
          <ThemeLink
            onClick={() => navigate('/blog/collection')}
            to={'/blog/collection'}
          >
            Senior Collection
          </ThemeLink>
        </div>

        {/* Blog Grid */}
        <div className={styles.blogsContainer}>
          <Container size={'large'}>
            <BlogPreviewGrid data={blogData} hideReadMoreOnWeb showExcerpt />
          </Container>
        </div>
      </div>
    </Layout>
  );
};

export default BlogPage;
